import React, {useState, useEffect} from "react";
import { Col, Row, Button, Dropdown, Form, InputGroup } from '@themesberg/react-bootstrap';

import axios from "axios";



import { AllowedUserTable } from "../../components/Tables";
//import { pageReport } from "../data/tables";

import { CircularProgress, LinearProgress } from "@mui/material";
import { faFileAlt, faUserAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useId } from "react";
const tempAllowedUsers = [
  { id: 1,  icon: faFileAlt, email: '', name: '', link: '<name>' },
];


export default () => {
  const [tableData, setTableData] = useState([]);
  const [bulkFile, setBulkFiles] = useState(null);
  const [reportFile, setReportFiles] = useState(null);
  const [bulkInput_ref, setBulkInputRef] = useState("");
  const [reportInput_ref, setReportInputRef] = useState("");
  const [msgText, setMSGText] = useState("");

  const [isSuccess, setIsSuccess] = useState(false);
  const [barProgress, setProgress] = useState(0);
  
  const updateTable = async() => {
    await fetch('/api/get-allowed-users')
    .then((response) => response.json())
    .then((data) => {
      setTableData(data.users)
    })
  };
  const timeout = (delay) => {
    return new Promise( res => setTimeout(res, delay) );
  }
  const tableLoop = async() => {
    while(true){
      await updateTable();
      await timeout(5000); //for 10 sec delay
    }
  }
  useEffect(() => {//ref={(ref) => { setFileInputRef(ref) }}
    var test = true
    // Update the document title using the browser API
    if (test) {
      test = false
      tableLoop();
      //updateTable();
    }
    setTableData(tempAllowedUsers);
    setProgress(0);
  }, []);

  const displayText = async(text) => {
    var s = ''
    for (let i = 0; i < text.length; i++) {
      await timeout(50);
      const element = text[i];
      s += element;
      setMSGText(s);
    }
    setMSGText(text);
    await timeout(2000);
    while (s.length > 0) {
      s = s.substring(0, s.length - 1);
      setMSGText(s);
      await timeout(25);
    }
    
    setMSGText("");
  }
  const resetProgress = async() => {
    await timeout(2000);
    while (barProgress > 2) {
      setProgress(barProgress - 5);
      await timeout(5);
    }
    setProgress(0);
  }
  const click = () => {
    if(emailText.length > 0){
      fetch('/api/add-allowed-user/'+emailText, { method: 'POST' })
      .then(updateTable);
      displayText('sent.');
    }
  }

  const bulkChangeHandler = (event) => {

    setBulkFiles(event.target.files[0])
  }

  const reportChangeHandler = (event) => {

    setReportFiles(event.target.files[0])
  }
  const handleKeyDown = (event) => {
    if (event.key === 'Enter') {
      event.preventDefault();
      click();
    }
  }
  const id = useId();
  const [input, setInput] = useState('');
  const [emailText, setEmailText] = useState('');
  return (
    <>
      <Row> <h1> User Access </h1> </Row>
      <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4">
      
      <Row>
        <Col xs={12} xl={38}>
          <Form>
            <Form.Group className="mb-3">
              <Form.Label htmlFor={id}>Email address { msgText }</Form.Label>
              <InputGroup id={id} value={input} onChange={e => setEmailText(e.target.value)}>
                <Form.Control type="text" placeholder="name@example.com" onKeyPress={handleKeyDown} />
                <InputGroup.Text ><FontAwesomeIcon icon={faUserAlt} /></InputGroup.Text>
              </InputGroup>
            </Form.Group>
          </Form>
          <Button onClick={()=>{click()}}>Add</Button>
        <Row>
          <Col xl={3}>
            <p>  </p>
          </Col>
          
        </Row>
         {/*  <GeneralInfoForm /> */}
        </Col>

        <div className="d-flex">
        </div>
        </Row>
      </div>

      <Row>
        <Col>
          <AllowedUserTable items={tableData} update={updateTable} />
        </Col>
      </Row>
    </>
  );
};

