export default "body {\n  --black: #000000;\n  --ash-black: #222;\n  --white: #fafafa;\n  --sky: #00ccff;\n  --green: #22dddd;\n  --blue: #1300ff;\n  --dusk: #6600ff;\n  --purple: #9900ff;\n  --pink: #ff0066;\n  --red: #fe0222;\n  --orange: #fd7702;\n  --yellow: #ffbb00;\n\n  --background: var(--dusk);\n  --accent: var(--white);\n\n  margin: 0;\n  padding: 0;\n  background-color: var(--background);\n  color: var(--accent);\n  padding-bottom: 100px;\n  overflow: hidden;\n}\n\n#root {\n  position: absolute;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n* {\n  font-family: sofia-pro, sans-serif;\n  font-weight: 400;\n  font-style: normal;\n  -webkit-font-smoothing: antialiased;\n}\n\nh1,\nh2,\nh3 {\n  font-family: sofia-pro, sans-serif;\n  font-weight: 600;\n  font-style: normal;\n}\n\nh1 {\n  font-size: 36px;\n  font-weight: 700;\n  letter-spacing: -1px;\n  line-height: 1.2;\n  text-align: center;\n  margin: 100px 0 40px;\n}\n\nh2 {\n  font-weight: 400;\n  margin: 50px 0 10px;\n}\n\np {\n  margin: 0 0 30px 0;\n  font-size: 18px;\n}\n\nfooter {\n  position: fixed;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  display: flex;\n  justify-content: flex-start;\n  align-items: center;\n  padding: 20px;\n  border-top: 1px dotted var(--accent);\n  background-color: transparent;\n  background-image: radial-gradient(\n    circle at 50% 50%,\n    var(--background) 20%,\n    transparent 20.5%,\n    transparent 49.5%,\n    var(--background) 50%\n  );\n\n  background-size: 15px 15px;\n  backdrop-filter: blur(20px);\n  -webkit-backdrop-filter: blur(20px);\n  -moz-backdrop-filter: blur(20px);\n  font-size: 14px;\n  line-height: 14px;\n}\n\nfooter svg {\n  margin-right: 20px;\n}\n\nfooter a {\n  text-decoration: none;\n  color: var(--accent);\n}\n\ncode {\n  font-family: input-mono, monospace;\n  font-weight: 400;\n  font-style: normal;\n}\n\n::-webkit-scrollbar {\n  height: 5px;\n  width: 5px;\n  background: var(--background);\n  -webkit-border-radius: 1ex;\n}\n\n::-webkit-scrollbar-thumb {\n  background: var(--accent);\n  -webkit-border-radius: 1ex;\n}\n\n::-webkit-scrollbar-corner {\n  background: #fff3;\n}\n\n.refresh {\n  padding: 5px;\n  position: absolute;\n  border: 1px dotted var(--accent);\n  border-radius: 5px;\n  width: 20px;\n  height: 20px;\n  top: 10px;\n  right: 10px;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  cursor: pointer;\n}\n\n.refresh path {\n  fill: var(--accent);\n}\n\n.box {\n  width: 150px;\n  height: 150px;\n  border-radius: 50%;\n  background: var(--accent);\n  margin: 0;\n}\n\n.menu {\n  filter: drop-shadow(1px 1px 1px #262B40);\n  width: 300px;\n}\n\nul {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n  background: #fafafa;\n  max-height: 200px;\n  overflow-y: scroll;\n}\n\nli {\n  color: #000000;\n  display: block;\n  \n}\n\nli:hover {\n  transform: scale(1.52);\n  /* color: #555; */\n  cursor: pointer;\n  background-color: #dedfe2;\n  border-radius: 25px;\n  transition: background-color .2s ease-in-out;\n  transition: border-radius .5s ease-in-out;\n}\n\nli:active {\n  cursor: pointer;\n  background-color: #303549;\n  border-radius: 10px;\n  transition: all .1s ease-in-out;\n}\n\nul,\nli {\n  list-style: none;\n  margin: 0;\n  padding: 10px;\n}\n\nbutton {\n  -webkit-appearance: button;\n  background: #fafafa;\n  color: #000000;\n  border: none;\n  border-radius: 10px;\n  padding: 10px 20px;\n  font-size: 18px;\n  font-weight: 700;\n  cursor: pointer;\n  width: 100%;\n  text-align: left;\n  margin-bottom: 10px;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\nbutton path {\n  fill: var(--background);\n}\n\ninput {\n  accent-color: var(--accent);\n}\n\n.controls {\n  display: flex;\n  flex-direction: column;\n  padding: 0;\n  padding-bottom: 50px;\n  align-items: center;\n}\n\nlabel {\n  display: flex;\n  align-items: center;\n  margin: 20px 0;\n}\n\ninput[type=\"number\"] {\n  border: 0;\n  border-bottom: 1px dotted var(--accent);\n  color: var(--accent);\n  margin-left: 10px;\n  background: var(--background);\n  width: 50px;\n}\n\ninput[type=\"number\"]:focus {\n  outline: none;\n  border-bottom: 2px solid var(--accent);\n}\n\ninput[type=\"number\"]::-webkit-inner-spin-button {\n  -webkit-appearance: none;\n}\n\n.example {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: flex-start;\n}\n\n.item {\n  width: 300px;\n  height: 300px;\n  display: flex;\n  align-items: flex-start;\n  justify-content: center;\n  background: #fafafa;\n  color: #000000;\n  font-size: 240px;\n  line-height: 260px;\n  font-weight: 700;\n  border-radius: 30px;\n}\n\n.menu {\n  position: absolute;\n  z-index: 3;\n}\n";