const timeout = (delay) => {
  return new Promise( res => setTimeout(res, delay) );
}
const P = styled.p`
  min-width: 30px;
`;
const GetStatus = forwardRef((props, ref) => {
  //status_info
  const [textInput, setTextInput] = useState("");
  const updateStatus = /* useCallback( */
    async(d) => {
      await d.then((status) => {
        if(typeof(status.status2) == 'string'){
          setText2(status.status2); 
        }
        if(typeof(status.status) == 'string'){
          setTextInput(status.status);
          return status.status;
        }
      })
      return ''
    }/* ,
    [textInput]
  ); */
  const [text2, setText2] = useState(
    typeof(props.status_info) == 'object' ? props.status_info.status2 : ''
  );
  const updateStatus2 = useCallback( 
    async(d) => {
      await d.then((status) => {
        
        if(typeof(status.status) == 'string'){
          setText2(status.status);
          return status.status;
        }else {//console.log(typeof(status.status));
        }
      })
      return ''
    },
    [text2]
  );
  
  const foreverUpdate = async(test) => {
    let text = props.ogStatus;
    setTextInput(text);
    let lastText = text;
    const maxTries = 25;
    let tries = 0;

    try{
      await timeout((props.len-props.index) * 1000);
      // console.log(`index: ${props.index}, ${props.len} - ${(props.len-props.index) * 1000}`)
    }catch(err){console.log(err)}

    while(text != 'done' && text != 'timed out' && text != 'error_logged' && test.stillGoing){
      //updateStatus(
        await fecthJSON('/api/get-status', props.file_name, props.process_id)
        .then((d) => {
          if(typeof(d.status) == 'string' && test.stillGoing){
            setTextInput(d.status);
            text = d.status;
          }else {//console.log(typeof(d.status));
          }
        })
      //);
      await timeout(Math.floor(5000 + Math.random() * 2000));
      if(lastText == text){tries++;}
      else{tries = 0; lastText = text;}
      if(tries>maxTries){break;}
    }
  }
  useEffect(() => {//ref={(ref) => { setFileInputRef(ref) }}
    var test = {stillGoing: true}
    //setTextInput(props.status, foreverUpdate());
    foreverUpdate(test)
    return () => test.stillGoing = false;
  }, []);
  return (
    <OverlayTrigger
    placement="top"
    trigger={['hover', 'focus']}
    overlay={
      <Tooltip>
        {textInput!=''?'google: '+textInput:''}
        {textInput!=''&&text2!=''?', ':''}
        {text2!=''?'excel: '+text2:''}</Tooltip>
    }>
  <P>{textInput}</P>
  </OverlayTrigger>);
});

function Name(props){
  const {name, og_name, og_name2, toastRef} = props;
  const canEdit = typeof(og_name) == 'string' || typeof(og_name2) == 'string';
  const [editing, setEditing] = useState(false);
  const [newName, setNewName] = useState(name.slice(0, -5));
  const [ext, setExt] = useState(name.slice(-5));
  const click = () => {
    if(editing){
      if(typeof(og_name) == 'string'){
        edit(og_name);
        toastRef.current.showMessage('Name change requested.', 3000);

      }
      // if(typeof(og_name2) == 'string'){
      //   edit(og_name2);
      // }
    }
    setEditing(!editing);
  }

  const edit = (og) => {
    fetch('/add-translation', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'key': 'super secret key',
      },
      body: JSON.stringify({ name: og, translation: newName+og.slice(-5)})
    })
    .then((response) => response.json())
    .then((data) => {
        if(typeof(data) == 'string'){
          var _data = JSON.parse(data);
        }else{var _data = data;}
        return _data;
    })
  }

  const onCHange = (e) => {
    if(typeof(e.target.value) == 'string' && e.target.value != ''){
      setNewName(e.target.value);
    }
  }

  const handleKeyDown = (event) => {
    if (event.key === 'Enter') {
      event.preventDefault();  // Form.Control refreshes the page on enter. this disables that feature
      click();
    }
  }

  return <>
  {editing ? 
  <Input defaultValue={newName} onChange={onCHange} onKeyDown={handleKeyDown} autoFocus onBlur={click}/> 
  : <Card.Link onClick={click}>{newName}</Card.Link>} 
  
  {canEdit ? 
  <>
  <div style={{display: "flex", float:"right"}}>
  {!editing && props.available ? <Card.Link onClick={click} style={{paddingLeft: '10px'}}>
    <FontAwesomeIcon icon={faPenAlt} /* onClick={click} style={{"&:hover": { cursor: "pointer" }}} *//> 
  </Card.Link> : ''}
  {props.available ? <Form style={{paddingLeft: '10px'}}>
    <Form.Check onChange={(e) => {props.func(e.target.checked, name.slice(0, -5), newName, og_name, og_name2)}}/>
  </Form> : ''}
  
  </div>
  {/* <Card.Link onClick={()=>{props.func(newName)}} style={{float:"right"}}>
    <FontAwesomeIcon icon={faTrashAlt} /> 
  </Card.Link> */}</>
  : ''}
  </>
}

