  const MyTextDisplay = forwardRef((props, ref) => {
    const [text, setText] = useState("");
    const onChangeText = useCallback(txt=> { setText(txt); }, [text]);
    const _displayText = async(_text) => {
      var s = ''
      for (let i = 0; i < _text.length; i++) {
        await timeout(10);
        const element = _text[i];
        s += element;
        setText(s);
      }
      setText(_text);
      // await timeout(2000);
      // while (s.length > 0) {
      //   s = s.substring(0, s.length - 1);
      //   setText(s);
      //   await timeout(5);
      // }
      
      // setText("");
    }
    const _removeText = async() => {
      var s = text
      while (s.length > 0) {
        s = s.substring(0, s.length - 1);
        setText(s);
        await timeout(5);
      }
      setText("");
    }
    useImperativeHandle(ref, () => ({
  
      async displayText (txt){
        await _displayText(txt);
      },
      async removeText(){
        await _removeText();
      }
  
    }));
    return (
      <p> &nbsp; {text} &nbsp; </p>
    );
  });

