import React, {useState} from 'react';
//import Video from '../../../videos/video.mp4';
import Image from '../../../videos/bg3.jpg';
import {HeroContainer, HeroBg, VideoBg, HeroContent, HeroH1, HeroP, HeroBtnWrapper, ArrowForward, ArrowRight, ImgBg, BackDrop} from './HeroElements';
import {Button} from '../ButtonElement';
const HeroSection = () => {
  const [hover, setHover] = useState(false);

  const onHover = () => {
    setHover(!hover);
  };
  return (
    <HeroContainer id="Home">
        <HeroBg>
            {/* <VideoBg autoPlay loop muted src={Video} type="video/mp4">

            </VideoBg> */}
            <ImgBg src={Image}></ImgBg>
        </HeroBg>
        <BackDrop>
          <HeroContent>
              <HeroH1> Junglr App </HeroH1>
              <HeroP> data management </HeroP>
              <HeroBtnWrapper>
                  <Button onClick={()=>{window.location.href="/dashboard/overview"}} smooth={true} duration={800} onMouseEnter={onHover} onMouseLeave={onHover}
                  primary="true" dark="true" fontbig="true"
                  > Go to Dashboard {hover ? <ArrowForward/> : <ArrowRight/>} </Button>
              </HeroBtnWrapper>
          </HeroContent>
        </BackDrop>
    </HeroContainer>
  )
}

export default HeroSection